import { createRouter, createWebHistory } from 'vue-router'
import { useAuthStore } from '@/store/auth'

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: () => import('@/pages/LoginView.vue'),
      meta: {
        title: '',
        requiresAuth: false,
        hideInMenu: true,
      },
    },
    {
      path: '/',
      component: () => import('@/components/LayoutView.vue'),
      meta: { requiresAuth: true },
      children: [
        {
          path: '',
          redirect: '/home',
        },
        {
          path: '/home',
          component: () => import('@/pages/HomeView.vue'),
          meta: { title: '', requiresAuth: true, layoutClass: 'layout-home' },
        },
        {
          path: '/containers',
          component: () => import('@/pages/ContainersView.vue'),
          meta: { title: '', requiresAuth: true, layoutClass: 'layout-containers' },
        },
        {
          path: '/containers/create',
          name: 'container-create',
          component: () => import('@/pages/CreateContainer/ContainerCreateView.vue'),
          meta: {
            title: '',
            requiresAuth: true,
            layoutClass: 'layout-container-create',
          },
        },
        {
          path: '/containers/:id',
          name: 'container-detail',
          component: () => import('@/pages/ContainerDetail/ContainerDetailView.vue'),
          meta: {
            title: '',
            requiresAuth: true,
            layoutClass: 'layout-container-detail',
          },
        },
        {
          path: '/images',
          component: () => import('@/pages/ImagesView.vue'),
          meta: { title: '', requiresAuth: true, layoutClass: 'layout-images' },
        },
        {
          path: '/compose',
          name: 'compose',
          component: () => import('@/pages/ComposeView.vue'),
          meta: { title: 'Compose ', requiresAuth: true },
        },
        {
          path: '/compose/create',
          name: 'compose-create',
          component: () => import('@/pages/ComposeCreateView.vue'),
          meta: {
            title: ' Compose ',
            requiresAuth: true,
            layoutClass: 'layout-compose-create',
          },
        },
        {
          path: '/compose/:projectName/detail',
          name: 'compose-detail',
          component: () => import('@/pages/ComposeDetailView.vue'),
          meta: {
            title: 'Compose ',
            requiresAuth: true,
            layoutClass: 'layout-compose-detail',
          },
        },
        {
          path: '/volumes',
          name: 'volumes',
          component: () => import('@/pages/VolumesView.vue'),
          meta: { title: 'Volume ', requiresAuth: true, layoutClass: 'layout-volumes' },
        },
        {
          path: '/volumes/:name',
          name: 'volume-detail',
          component: () => import('@/pages/VolumeDetailView.vue'),
          meta: {
            title: 'Volume ',
            requiresAuth: true,
            layoutClass: 'layout-volume-detail',
          },
        },
        {
          path: '/networks',
          name: 'networks',
          component: () => import('@/pages/NetworksView.vue'),
          meta: { title: '', requiresAuth: true, layoutClass: 'layout-networks' },
        },
        {
          path: '/networks/:id',
          name: 'network-detail',
          component: () => import('@/pages/NetworkDetailView.vue'),
          meta: {
            title: '',
            requiresAuth: true,
            layoutClass: 'layout-network-detail',
          },
        },
        {
          path: '/settings',
          component: () => import('@/pages/SettingsView.vue'),
          meta: { title: '', requiresAuth: true, layoutClass: 'layout-settings' },
        },
        {
          path: '/logs',
          component: () => import('@/pages/LogsPageView.vue'),
          meta: { title: '', requiresAuth: true, layoutClass: 'layout-logs' },
        },
        {
          path: '/terminal',
          component: () => import('@/pages/TerminalView.vue'),
          meta: { title: '', requiresAuth: true, layoutClass: 'layout-terminal' },
        },
      ],
    },

    {
      // 404 
      path: '/:pathMatch(.*)*',
      redirect: '/home',
    },
  ],
})

// 
router.beforeEach(async (to, _from, next) => {
  const authStore = useAuthStore()

  // 
  if (authStore.checkingAuth) {
    // 
    while (authStore.checkingAuth) {
      await new Promise((resolve) => setTimeout(resolve, 50))
    }
  }

  // 
  if (to.path === '/login') {
    // 
    if (authStore.isLoggedIn || !authStore.authEnabled) {
      next('/')
      return
    }
    // 
    next()
    return
  }

  // 
  if (authStore.authEnabled && !authStore.isLoggedIn) {
    if (to.path !== '/login') {
      next('/login')
      return
    }
  }

  // 
  next()
})

export default router

export function navigateTo(path: string) {
  router.push(path)
}
